import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {
    Grid,
    Segment,
    Loader,
} from 'semantic-ui-react'
import NebPay from 'nebpay';
import attachScrollSwitchPage from '../utils/attachSrcollSwitchPage';
import { Input, TextArea, Button, Header, Icon, Modal, Message  } from 'semantic-ui-react'
import { register } from "../dataAdapter";

class RegisterPage extends Component {
    state = {
        modalOpen: false,
        name1: '',
        id1: '',
        name2: '',
        id2: '',
        content: '',
        errorMessage: false,
        ifFetch: false,
    }
    handleOpen = () => {
        const { name1, id1, name2, id2, content } = this.state;
        if (!id1 || !id2) {
            this.setState({
                errorMessage: true,
            })
            setTimeout(() => {
                this.setState({
                    errorMessage: false,
                })
            }, 2000);
            return;
        }
        this.setState({
            modalOpen: true,
            ifFetch: true,
        });
        register(name1, id1, name2, id2, content)
            .then(res => {
                this.setState({
                    ifFetch: false,
                });
                console.log(res);
            })
    }
    handleClose = () => {
        this.setState({
            modalOpen: false,
        })
    }
    handleInputChange = (type) => (e) => {
        this.setState({
            [type]: e.target.value,
        })
    }
    render() {
        return (
            <Segment style={{ padding: '8em 0em' }} vertical>
                <Grid container stackable verticalAlign='middle'>
                    <Grid.Row>
                        <Grid.Column floated='left' width={8}>
                            <div style={{ width: '410px', margin: '0 auto' }}>
                                <div style={{ textAlign: 'center', whiteSpace: 'nowrap' }}>
                                    <Input
                                        onChange={this.handleInputChange('name1')}
                                        icon='heart' iconPosition='left' placeholder='Type your name...' />
                                    <Input
                                        onChange={this.handleInputChange('id1')}
                                        icon='id card'
                                        iconPosition='left'
                                        style={{ marginLeft: '12px' }}
                                        placeholder='Type your identification...' />
                                </div>
                                <div style={{ fontSize: '20px', textAlign: 'center', margin: '16px 0' }}>with</div>
                                <div style={{ textAlign: 'center', whiteSpace: 'nowrap' }}>
                                    <Input
                                        onChange={this.handleInputChange('name2')}
                                        icon='heart' iconPosition='left' placeholder="Type the opponent's name..." />
                                    <Input
                                        onChange={this.handleInputChange('id2')}
                                        icon='id card'
                                        iconPosition='left'
                                        placeholder="Type the opponent's identification..."
                                        style={{ marginLeft: '12px' }}
                                    />
                                </div>
                                <div style={{ fontSize: '20px', textAlign: 'center', margin: '16px 0' }}>make a promise</div>
                                <div style={{ textAlign: 'center' }}>
                                <TextArea
                                    onChange={this.handleInputChange('content')}
                                    autoHeight
                                    placeholder='Record the love of two of you'
                                    style={{ minWidth: '400px' }}
                                />
                                </div>

                                <div style={{ textAlign: 'center', marginTop: '12px' }}>
                                    <Modal
                                        trigger={
                                            <div>
                                                <Button onClick={this.handleOpen} style={{ background: '#f699b4', color:'#fff'}}>Save Words</Button>
                                                <Message
                                                    floating
                                                    error
                                                    style={{
                                                        position: "absolute",
                                                        whiteSpace: "nowrap",
                                                        transform: "translateX(-50%)",
                                                        left: "50%",
                                                        textAlign: 'left'
                                                    }}
                                                    hidden={!this.state.errorMessage}
                                                    header='input error'
                                                    content='Identification is not allowed to be empty'
                                                />
                                            </div>
                                        }
                                        open={this.state.modalOpen}
                                        onClose={this.handleClose}
                                        basic
                                        size='small'
                                    >
                                        <Header icon='browser' content='Transaction Padding' />
                                        <Modal.Content>
                                            {
                                                this.state.ifFetch
                                                    ? <Loader as="h3" active inline='centered'>Writing on the chain</Loader>
                                                    : <h3>Transaction is success! Data is writing to the blockchain and it will takes a certain amount of time.</h3>
                                            }

                                        </Modal.Content>
                                        <Modal.Actions>
                                            <Button color='green' onClick={this.handleClose} inverted>
                                                <Icon name='checkmark' /> Got it
                                            </Button>
                                        </Modal.Actions>
                                    </Modal>
                                </div>
                            </div>

                        </Grid.Column>
                        <Grid.Column width={6}>
                            <Header as='h3' style={{ fontSize: '2em' }}>One id will only be registered once in the chain,
                                Never be tampered, Never be deleted</Header>
                            <p style={{ fontSize: '1.33em' }}>
                                In the smart contract, we set the id of a couple to be registered only once, after submit which the registration will not be written in the chain.
                            </p>
                            <Header as='h3' style={{ fontSize: '2em' }}>Declare Love to The World</Header>
                            <p style={{ fontSize: '1.33em' }}>
                                All users can query the couple's name and love words by one identification
                            </p>
                        </Grid.Column>
                    </Grid.Row>
                    {/*<Grid.Row>*/}
                        {/*<Grid.Column textAlign='center'>*/}
                            {/*<Button size='huge'>Check Them Out</Button>*/}
                        {/*</Grid.Column>*/}
                    {/*</Grid.Row>*/}
                </Grid>
            </Segment>
        );
    }
}

RegisterPage.propTypes = {};
RegisterPage.defaultProps = {};

export default attachScrollSwitchPage(RegisterPage, 'register');
