import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {
    Grid,
    Header,
    Segment,
    Button,
    Input,
    Modal,
    Icon,
    Loader,
} from 'semantic-ui-react'
import attachScrollSwitchPage from '../utils/attachSrcollSwitchPage';
import { check } from "../dataAdapter";
import ShowLove from '../components/ShowLove';


class InquirePage extends Component {
    state = {
        ifFetch: true,
        modalOpen: false,
        id: '',
        message: {
            name1: '',
            name2: '',
            content: '',
        },
        message2: '',
    }
    handleInputChange = (type) => (e) => {
        this.setState({
            [type]: e.target.value
        })
    }
    handleClose = () => {
        this.setState({
            modalOpen: false,
        })
    }
    handleOpen = () => {
        this.setState({
            ifFetch: true,
            modalOpen: true,
        });
        check(this.state.id)
            .then(res => {
                const { message, name1, name2, content } = res;
                debugger
                this.setState({
                    message2: message,
                    message: {
                        name1,
                        name2,
                        content,
                    },
                    ifFetch: false,
                })
            })
    }
    render() {
        return (
            <Segment style={{ padding: '0em' }} vertical>
                <Grid celled='internally' columns='equal' stackable>
                    <Grid.Row textAlign='center'>
                        <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em' }}>
                            <Header as='h3' style={{ fontSize: '2em' }}>"Love inquiry"</Header>
                            <p style={{ fontSize: '1.33em' }}>You can query the love statement through a registered id</p>
                        </Grid.Column>
                        <Grid.Column style={{ paddingBottom: '5em', paddingTop: '5em' }}>
                            <div>
                                <Input
                                    icon='id card'
                                    iconPosition='left'
                                    onChange={this.handleInputChange('id')}
                                    placeholder="Type a register identification for querying"
                                    style={{ marginLeft: '12px', width: '400px' }}
                                />
                            </div>
                            <div style={{ textAlign: 'center' }}>
                                <Modal
                                    trigger={<Button onClick={this.handleOpen} primary size='large' style={{ backgroundColor: '#F06292', marginTop: '20px' }}>Inquire</Button>}
                                    open={this.state.modalOpen}
                                    onClose={this.handleClose}
                                    basic
                                    size='small'
                                >
                                    <Header icon='browser' content={this.state.ifFetch ? 'Querying' : 'Love Statement'} />
                                    <Modal.Content>
                                        {
                                            this.state.ifFetch
                                                ? <Loader as="h3" active inline='centered'>reading data from block</Loader>
                                                : null
                                        }
                                        {
                                            !this.state.ifFetch && !this.state.message2
                                                ? <ShowLove name1={this.state.message.name1} name2={this.state.message.name2} content={this.state.message.content} />
                                                : null
                                        }
                                        {
                                            !this.state.ifFetch && this.state.message2
                                                ? <h3>Oops... {this.state.message2}</h3>
                                                : null
                                        }
                                    </Modal.Content>
                                    <Modal.Actions>
                                        <Button color='green' disabled={!this.state.id} onClick={this.handleClose} inverted>
                                            <Icon name='checkmark' /> Got it
                                        </Button>
                                    </Modal.Actions>
                                </Modal>
                            </div>
                        </Grid.Column>
                    </Grid.Row>
                </Grid>
            </Segment>
        );
    }
}

InquirePage.propTypes = {};
InquirePage.defaultProps = {};

export default attachScrollSwitchPage(InquirePage, 'about');
