import PropTypes from 'prop-types'
import React, { Component } from 'react'
import {
    Container,
    Grid,
    Header,
    Icon,
    List,
    Menu,
    Responsive,
    Segment,
    Sidebar,
    Visibility,
} from 'semantic-ui-react'
import RegisterPage from './pages/RegisterPage'
import InquirePage from './pages/InquirePage'
import HomePage from './pages/Homepage';
import HowToUsePage from './pages/HowToUsePage';
import { switchPage, getPagePath } from "./utils/attachSrcollSwitchPage";

/* eslint-disable react/no-multi-comp */
/* Heads up! HomepageHeading uses inline styling, however it's not the best practice. Use CSS or styled components for
 * such things.
 */

/* Heads up!
 * Neither Semantic UI nor Semantic UI React offer a responsive navbar, however, it can be implemented easily.
 * It can be more complicated, but you can create really flexible markup.
 */
class PageNav extends Component {
    state = {
        pagePath: 'homepage'
    }
    handleClick = (path) => () => {
        this.setState({
            pagePath: path,
        })
        switchPage(path)
    }
    render() {
        return (
            <React.Fragment>
                <Menu.Item
                    onClick={this.handleClick('homepage')}
                    active={getPagePath() === 'homepage'}>Home
                </Menu.Item>
                <Menu.Item
                    onClick={this.handleClick('register')}
                    active={getPagePath() === 'register'}
                >Register</Menu.Item>
                <Menu.Item
                    onClick={this.handleClick('about')}
                    active={getPagePath() === 'about'}>
                    Inquire
                </Menu.Item>
                <Menu.Item
                    onClick={this.handleClick('howtouse')}
                    active={getPagePath() === 'howtouse'}>
                    HOW TO USE
                </Menu.Item>
            </React.Fragment>
        )
    }
}

class DesktopContainer extends Component {
    state = {}

    hideFixedMenu = () => this.setState({ fixed: false })
    showFixedMenu = () => this.setState({ fixed: true })

    render() {
        const { children } = this.props
        const { fixed } = this.state

        return (
            <Responsive minWidth={768}>
            <Visibility
                once={false}
                onBottomPassed={this.showFixedMenu}
                onBottomPassedReverse={this.hideFixedMenu}
            >
                <Segment inverted textAlign='center' style={{
                    minHeight: 700, padding: '1em 0em',
                    backgroundColor: '#f699b4'
                }} vertical>
                    <Menu
                        fixed={fixed ? 'top' : null}
                        inverted={!fixed}
                        pointing={!fixed}
                        secondary={!fixed}
                        size='large'
                        style={{
                            backgroundColor: '#f798b4',
                            color: '#fff'
                        }}
                    >
                        <Container>
                            <PageNav />
                        </Container>
                    </Menu>
                    <HomePage />
                </Segment>
            </Visibility>

                {children}
            </Responsive>
    )
    }
}

DesktopContainer.propTypes = {
    children: PropTypes.node,
}


class MobileContainer extends Component {
    state = {}

    handlePusherClick = () => {
        const { sidebarOpened } = this.state

        if (sidebarOpened) this.setState({ sidebarOpened: false })
    }

    handleToggle = () => this.setState({ sidebarOpened: !this.state.sidebarOpened })

    render() {
        const { children } = this.props
        const { sidebarOpened } = this.state

        return (
            <Responsive {...Responsive.onlyMobile}>
                <Sidebar.Pushable>
                    <Sidebar
                        as={Menu} animation='uncover' inverted vertical visible={sidebarOpened}
                        style={{ backgroundColor: '#f699b4'}}
                    >
                        <PageNav />
                    </Sidebar>

                    <Sidebar.Pusher dimmed={sidebarOpened} onClick={this.handlePusherClick} style={{ minHeight: '100vh' }}>
                        <Segment inverted
                                 textAlign='center'
                                 style={{
                                     minHeight: 350, padding: '1em 0em',
                                     backgroundColor: '#f699b4'
                                 }} vertical>
                            <Container>
                                <Menu inverted pointing secondary size='large'>
                                    <Menu.Item onClick={this.handleToggle}>
                                        <Icon name='sidebar' />
                                    </Menu.Item>
                                </Menu>
                            </Container>
                            <HomePage mobile />
                        </Segment>

                        {children}
                    </Sidebar.Pusher>
                </Sidebar.Pushable>
            </Responsive>
        )
    }
}

MobileContainer.propTypes = {
    children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
    <div>
        <DesktopContainer>{children}</DesktopContainer>
        <MobileContainer>{children}</MobileContainer>
    </div>
)

ResponsiveContainer.propTypes = {
    children: PropTypes.node,
}

const HomepageLayout = () => (
    <ResponsiveContainer>
        <RegisterPage />
        <InquirePage />
        <HowToUsePage />
        <Segment inverted vertical style={{ padding: '5em 0em' }}>
            <Container>
                <Grid divided inverted stackable>
                    <Grid.Row>
                        <Grid.Column width={3}>
                            <Header inverted as='h4' content='About' />
                            <List link inverted>
                                <List.Item as='a' href="mailto:keshidong.whu@gmail.com">Contact Us</List.Item>
                                <List.Item>The DAPP is based on <a href="https://nebulas.io/">Nebulas</a></List.Item>
                            </List>
                        </Grid.Column>
                        <Grid.Column width={3}>
                            <Header as='h4' inverted>About <a href="https://nebulas.io">Nebulas</a></Header>
                            <p>Nebulas is a next generation public blockchain, aiming for a continuously improving ecosystem.</p>
                        </Grid.Column>
                        <Grid.Column width={7}>
                            <Header inverted as='h4' content='DAPP' />
                            <List link inverted>
                                <List.Item as='a'>DApp is an abbreviated form for decentralized application.</List.Item>
                                <List.Item as='a'>A DApp has its backend code running on a decentralized peer-to-peer network. </List.Item>
                                <List.Item as='a'>Contrast this with an app where the backend code is running on centralized servers.</List.Item>
                            </List>
                            </Grid.Column>
                    </Grid.Row>
                </Grid>
            </Container>
        </Segment>
    </ResponsiveContainer>
)
export default HomepageLayout