import NebPay from '../utils/nebpay';
import config from '../config';

const nebPay = new NebPay();
const env = process.env.env;

console.log('环境配置..', config[env]['contract_address']);

export const register = (name1, id1, name2, id2, content) => {
    return new Promise((resolve) => {
        nebPay.call(config[env]['contract_address'], 0, 'register',
            JSON.stringify([name1, id1, name2, id2, content]), {
            qrcode: {
                showQRCode: false
            },
            // goods: {
            //     name: "test",
            //     desc: "test goods"
            // },
            //set listener for extension transaction result
            listener: (res) => {
                resolve(res);
            }
        });
    });
}

export const check = (id) => {
    return new Promise((resolve) => {
        nebPay.simulateCall(config[env]['contract_address'], 0, 'check', JSON.stringify([ id ]), {
            qrcode: {
                showQRCode: false
            },
            // goods: {
            //     name: "test",
            //     desc: "test goods"
            // },
            //set listener for extension transaction result
            listener: (res) => {
                resolve(JSON.parse(res.result));
            }
        });
    });
};
