import React, { Component } from 'react';


const pageScrollHandlers = {};
let currentPagePath = 'homepage';
export const switchPage = (path) => {
    const scrollHandler =  pageScrollHandlers[path] || (() => {});
    currentPagePath = path;
    scrollHandler();
}

export const getPagePath = () => {
    return currentPagePath;
}
// not good
// todo
const attachScrollSwitchPage = (Page, path) => {
    return class PageWrap extends Component {
        componentDidMount() {
            pageScrollHandlers[path] = () => {
                this.anchor.scrollIntoView({  behavior: 'smooth' });
            };
        }
        render() {
            return (
                <div ref={(dom) => {this.anchor = dom; }}>
                    <Page />
                </div>
            );
        }
    }
};

export default attachScrollSwitchPage;