import React from 'react';
import {
    Button,
    Container,
    Header,
    Icon,
} from 'semantic-ui-react'
import PropTypes from 'prop-types'
import Bubble from '../components/Bubble';

import {switchPage} from "../utils/attachSrcollSwitchPage";
import attachScrollSwitchPage from '../utils/attachSrcollSwitchPage';

const Homepage = ({ mobile }) => (
    <Container text>
        <Bubble />
        <Header
            as='h1'
            content='True Love Registration'
            inverted
            style={{
                fontSize: mobile ? '2em' : '4em',
                fontWeight: 'normal',
                marginBottom: 0,
                marginTop: mobile ? '1.5em' : '3em',
            }}
        />
        <Header
            as='h2'
            content="One id can only be registered once."
            inverted
            style={{
                fontSize: mobile ? '1.5em' : '1.7em',
                fontWeight: 'normal',
                marginTop: mobile ? '0.5em' : '1.5em',
            }}
        />
        <Button
            primary size='huge'
            onClick={() => {
                switchPage('register');
            }}
        >
            register free
            <Icon name='right arrow' />
        </Button>
    </Container>
)

Homepage.propTypes = {
    mobile: PropTypes.bool,
}

export default attachScrollSwitchPage(Homepage, 'homepage');